@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  src: url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  margin: 0;
}
.page-header-wrapper {
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  justify-content: space-between;
  padding: 11px 20px;
  width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
}
.page-header__logo-image {
  box-sizing: border-box;
}
.age-header__logo-text {
  margin-left: 0;
  padding-left: 0;
}
.page-header__logo-wrapper {
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .page-header__logo-name {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 25px;
    pointer-events: none;
    cursor: default;
  }
  .page-header-wrapper {
    display: flex;
    flex-direction: column;
  }
  .page-header__logo-image:hover {
    opacity: 0.8;
  }
  .page-header__logo-image:active {
    opacity: 0.6;
  }
}
@media (min-width: 768px) {
  .page-header__logo-image {
    width: 173px;
    height: auto;
  }
  .page-header__logo-name {
    display: none;
  }
  .page-header-wrapper {
    width: 768px;
    margin: 0 auto;
    border-bottom: none;
    padding: 0 30px;
    margin-top: 43px;
    display: flex;
    justify-content: space-between;
  }
  .page-header__logo-wrapper {
    margin-top: -20px;
  }
  .page-header__logo-wrapper:hover {
    opacity: 0.8;
  }
}
@media (min-width: 1440px) {
  .page-header {
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
  }
  .page-header__logo-svg {
    width: 200px;
    height: auto;
  }
  .page-header--background::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 695px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-size: 50%;
    background-repeat: no-repeat;
    display: block;
    background-position: right center;
    z-index: -1;
  }
  .page-header-wrapper {
    width: 1300px;
    padding: 0 40px;
    margin-top: 74px;
  }
  .page-header--background .site-list__item--active {
    border-bottom: 2px solid #ffffff;
  }
}
@media (min-width: 1440px) and (min-resolution: 144dpi), (min-width: 1440px) and (min-resolution: 1.5dppx) {
  .page-header--background::before {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}
.main-nav {
  width: 100%;
}
.main-nav__site-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.main-nav__toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 25px;
  border: none;
  background-color: #ffffff;
  display: block;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__site-list {
    display: none;
  }
  .main-nav__site-list {
    padding-top: 10px;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 3px;
    right: 0;
    width: 25px;
    height: 25px;
    background-image: url("../img/icon-mobile-menu.svg");
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .main-nav--opened .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 5px;
    right: 0;
    width: 18px;
    height: 18px;
    background-image: url("../img/icon-close.svg");
    background-repeat: no-repeat;
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .main-nav__site-list {
    display: flex;
  }
